.layout {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  max-width: 100vw;

  a:any-link {
    text-decoration: none;
  }

  h2 {
    text-align: center;
    padding: .3em;
    font-weight: bold;
  }

  &-socialnav {
    background-color: white;
    svg {
      width: 1em;
      height: 1em;
      fill: #282c34;
    }
  }

  &-container {
    flex: 1;
  }

  &-content {
    color: white;
    padding: .5em;
  }

  @media screen and (min-width: 600px) {
    &-content {
      max-width: 75vw;
      display: block;
      margin: 0 auto;
    }
  }

  @media screen and (min-width: 769px) {

    &-socialnav {
      display: none;
    }

    .menu {
      display: block;
      position: initial;

      ul {
        display: flex;
        justify-content: center;
        font-size: .8em;
      }
    }
  }

  @media screen and (min-width: 1025px) {
    &-content {
      max-width: 60vw;
    }
  }
}
