.footer {
  background-color: white;
  padding: .2em;

  svg {
    width: 1em;
    height: 1em;
    fill: black;
    &:hover {
      fill: grey;
    }
  }

  &-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &-info {
    font-size: .8em;
    font-style: italic;
  }

  @media screen and (min-width: 600px) {
    &-container {
      max-width: 75vw;
      display: block;
      margin: 0 auto;
    }
  }

  @media screen and (min-width: 769px) {
    &-container {
      max-width: 60vw;
    }
  }

  @media screen and (min-width: 1025px) {
    &-container {
      max-width: 50vw;
    }
  }
}
