@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after{content:'';content:none}q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

body{font-family:'Lato', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:calc(10px + 2vmin)}.text-bold{font-weight:bold}

.layout{background-color:#282c34;display:flex;flex-direction:column;min-height:100vh;max-width:100vw}.layout a:-webkit-any-link{text-decoration:none}.layout a:any-link{text-decoration:none}.layout h2{text-align:center;padding:.3em;font-weight:bold}.layout-socialnav{background-color:white}.layout-socialnav svg{width:1em;height:1em;fill:#282c34}.layout-container{flex:1 1}.layout-content{color:white;padding:.5em}@media screen and (min-width: 600px){.layout-content{max-width:75vw;display:block;margin:0 auto}}@media screen and (min-width: 769px){.layout-socialnav{display:none}.layout .menu{display:block;position:static;position:initial}.layout .menu ul{display:flex;justify-content:center;font-size:.8em}}@media screen and (min-width: 1025px){.layout-content{max-width:60vw}}

.menu{background-color:white;position:absolute;top:4.7em;width:100%;display:none;z-index:1}.menu-toggle{display:block}.menu ul{list-style:none}.menu li{padding:.8em;text-align:center}.menu a{color:black}

.header{background-color:#20232a}.header-content{display:flex;align-items:center;color:white}.header-loader{padding:.5em}.header-imgCover{margin-left:.5em;padding:.5em}.header-img{background-color:white;border-radius:50%;width:4em}.header-title{flex:1 1;margin-left:.5em}.header-title small{font-size:.6em;font-style:italic}.header-iconMenu svg{width:1.5em;height:1.5em;fill:#ffffff;padding:.8em}.header-socialnav{display:none}@media screen and (min-width: 600px){.header-container{max-width:75vw;display:block;margin:0 auto}}@media screen and (min-width: 769px){.header-socialnav{display:block}.header-socialnav svg{width:1em;height:1em;fill:#ffffff}.header-socialnav svg:hover{fill:grey}.header-iconMenu{display:none}}@media screen and (min-width: 1025px){.header-container{max-width:50vw}}

.socialnav{display:flex;justify-content:center;font-size:1.2em}.socialnav a{color:#20232a;padding:.3em}

.footer{background-color:white;padding:.2em}.footer svg{width:1em;height:1em;fill:black}.footer svg:hover{fill:grey}.footer-content{display:flex;align-items:center;justify-content:space-between}.footer-info{font-size:.8em;font-style:italic}@media screen and (min-width: 600px){.footer-container{max-width:75vw;display:block;margin:0 auto}}@media screen and (min-width: 769px){.footer-container{max-width:60vw}}@media screen and (min-width: 1025px){.footer-container{max-width:50vw}}

