.header {
  background-color: #20232a;

  &-content {
    display: flex;
    align-items: center;
    color: white;
  }

  &-loader {
    padding: .5em;
  }

  &-imgCover {
    margin-left: .5em;
    padding: .5em;
  }

  &-img {
    background-color: white;
    border-radius: 50%;
    width: 4em;
  }

  &-title {
    flex: 1;
    margin-left: .5em;
    small {
      font-size: .6em;
      font-style: italic;
    }
  }

  &-iconMenu {
    svg {
      width: 1.5em;
      height: 1.5em;
      fill: #ffffff;
      padding: .8em;
    }
  }

  &-socialnav {
    display: none;
  }

  @media screen and (min-width: 600px) {
    &-container {
      max-width: 75vw;
      display: block;
      margin: 0 auto;
    }
  }

  @media screen and (min-width: 769px) {
    &-socialnav {
      display: block;
      svg {
        width: 1em;
        height: 1em;
        fill: #ffffff;
        &:hover {
          fill: grey;
        }
      }
    }

    &-iconMenu {
     display: none;
    }
  }

  @media screen and (min-width: 1025px) {
    &-container {
      max-width: 50vw;
    }
  }
}
